import React, { useEffect, useState } from "react";
import axios from 'axios';
import BootPay from "bootpay-js";
import {Button} from "@mui/material";
import {useCookies} from "react-cookie";

export default function Payment(){
    const [userEmail, setUserEmail] = useState('');
    const [cookies] = useCookies(['token']);

    useEffect(() => {
        const fetchUserEmail = async () => {
            const token = cookies.token;

            if (token) {
                try {
                    const userDetails = await axios.get('/api/auth/currentUser', {
                        headers: {
                            Authorization: `Bearer ${token}`
                        }
                    });
                    setUserEmail(userDetails.data.userEmail);
                } catch (error) {
                    console.error("Error fetching user email:", error);
                }
            }
        };

        fetchUserEmail();
    }, [cookies.token]);

    const onPayment = (duration) => {
        const productName = ` ${duration} `;

        //   
        let price;
        switch (duration) {
            case 30:
                price = '100';
                break;
            case 90:
                price = '200';
                break;
            case 180:
                price = '300';
                break;
            default:
                alert("   .");
                return;
        }

        BootPay.request({
            price: price,
            application_id: "651fdab0d25985001abb83d6",
            name: productName,
            pg: 'inicis',
            method: 'card', //,      .
            show_agree_window: 0, //      
            order_id: `premium${duration}`, // ,    .
        }).error(function (data) {
            alert(" ")
            console.log(data);
        }).cancel(function (data) {
            alert(" ")
            console.log(data);
        }).ready(function (data) {
            //      .
            console.log(data);
        }).confirm(function (data) {
            console.log(data);
            var enable = true; //       
            if (enable) {
                BootPay.transactionConfirm(data); //     .
            } else {
                BootPay.removePaymentWindow(); //         .
            }
        }).close(function (data) {
            //   . (,,   )
            console.log(data);
        }).done(function (data) {
            alert(" ")
            console.log(data);

            axios.post('/api/save-payment', {
                userEmail: userEmail,
                days: duration
            })
                .then(response => {
                    console.log("Payment data saved:", response.data);
                })
                .catch(error => {
                    console.error("Error saving payment data:", error);
                });
        });
    };

    return (
        <div>
            <Button variant="contained" color="primary" onClick={() => onPayment(30)}>30 </Button>
            <Button variant="contained" color="primary" onClick={() => onPayment(90)}>90 </Button>
            <Button variant="contained" color="primary" onClick={() => onPayment(180)}>180 </Button>
        </div>
    );
}
